body {
	font-family: "Helvetica Neue", sans-serif;
	font-weight: 400;
	color: $color-white;
}

.heading-primary {
	font-size: $text-xl;
	color: $color-mint;
	font-weight: 300;
	padding-bottom: 2rem;
}

.heading-secondary {
	font-size: $text-s;
	font-weight: 500;
	color: $color-mint;
	padding-bottom: 1rem;
	text-align: center;
}

.heading-tertiary {
	font-size: $text-s;
	font-weight: 400;
	padding-bottom: 1rem;
}

.paragraph {
	font-size: $text-xs;
	font-weight: 300;
	hyphens: auto;
}

.text-link {
	&,
	&:link,
	&:visited {
		font-size: $text-l;
		color: $color-mint;
		font-weight: 300;

		text-decoration: none;
		border: none;
		cursor: pointer;

		transition: all 0.1s ease-in;
	}
	&:hover {
		color: $color-purple;
	}
}
