.friends {
	visibility: hidden;
	&_container {
		width: $slider-size;
		padding-top: 12rem;

		display: grid;
		align-items: end;
		justify-items: center;

		background: rgba($color-black, 0.5);

		@include tablet {
			width: 100vw;
		}
	}
	&_list {
		width: 90%;
		&-heading {
			color: $color-mint;
			font-size: $text-m;
			margin-bottom: 2rem;
		}
		&-list {
			margin: auto;
			overflow: auto;
			list-style: none;
			&-item {
				margin-bottom: 2rem;
				display: grid;
				grid-template-columns: repeat(3, 1fr);
				align-items: center;

				svg {
					width: 5rem;
					height: 5rem;
				}
				p {
					font-size: $text-s;
					font-weight: 400;
				}
				button {
					width: 5rem;
				}
			}
		}
	}
}

#friends:target {
	visibility: visible;
}

.friends_container {
	.profile_user-icon {
		border: 2px solid $color-purple;
		border-radius: 25rem;
		padding: $margin;
		margin-bottom: $margin;
		width: 17rem;
	}
}
