@keyframes bg-anim-flash {
  0%,
  5% {
    opacity: 0.2;
    transform: translate3d(10px, var(5px), 0);
  }
  5.5%,
  100% {
    opacity: 0;
    transform: translate3d(0, 0, 0);
  }
}

//SLIDE IN ANIMATIONS

@keyframes sliderIn {
  to {
    transform: translateX(-100vw + $slider-size);
  }
}

@keyframes sliderOut {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100vw + $slider-size);
  }
}

@keyframes sliderFullIn {
  from {
    transform: translateX(-100vw + $slider-size);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes slide-right {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes gameIn {
  to {
    transform: translateY(-50%) translateX(0);
  }
}

@keyframes menuIn {
  to {
    transform: translateX(0);
  }
}

@keyframes navIn {
  to {
    transform: translateX(68vw);
  }
}

@keyframes navOut {
  from {
    transform: translateX(68vw);
  }
  to {
    transform: translateX(0);
  }
}
