form {
	width: 80%;

	div {
		padding-bottom: 1rem;
	}
}

input {
	width: 70%;
	padding-bottom: 0.5rem;

	background-color: transparent;
	border: none;
	border-bottom: 2px solid $color-purple;

	font-family: inherit;
	font-size: $text-m;
	font-weight: 300;
	color: rgba($color-white, 1);

	&:focus {
		border-bottom: 2px solid $color-mint;
		outline: none;
		&:invalid + label:after {
			visibility: visible;
		}
		&:valid + label:after {
			visibility: visible;
			content: "-";
			color: $color-mint;
		}
	}
	&::placeholder {
		font-family: inherit;
		font-weight: 300;
		color: rgba($color-white, 0.5);
	}

	&[type="password"] {
		font-family: inherit;
		font-weight: 300;
		color: rgba($color-white, 0.5);
	}
}

label {
	display: grid;
	grid-template-columns: 2fr 1fr;
	width: 70%;

	opacity: 1;
	transition: all 0.2s ease-in;

	font-size: 1.5rem;
	font-weight: 500;
	color: $color-purple;

	&:after {
		content: "x";
		color: $color-red;
		justify-self: right;
		visibility: hidden;
	}
}

input:placeholder-shown + label {
	opacity: 0;
	transform: translateY(-1rem);
}

textarea {
	resize: none;
}

select {
	-webkit-appearance: none;
	appearance: none;
	width: 70%;
	background-color: transparent;
	border: none;
	border-bottom: 2px solid $color-purple;

	font-family: inherit;
	font-size: 2.8rem;
	font-weight: 300;
	color: $color-white;

	option {
		color: $color-black;
		font-size: 1.8rem;
	}
}
