.u-modal {
  width: 100vw;
  height: 100vh;

  position: absolute;
  top: 0;
  left: 0;

  background: rgba($color-black, 0.7);

  @include tablet {
    position: fixed;
    z-index: 100;
  }
}

.u-close {
  width: 3.5rem;
  height: 3.5rem;
  cursor: pointer;

  z-index: 101;
  position: absolute;
  top: $margin;
  left: $margin;

  @include tablet {
    position: fixed;
    z-index: 102;
  }
}

.u-modal-heading {
  color: $color-purple;
  font-size: 3rem;
  font-weight: 300;

  border-bottom: 2px solid;

  position: absolute;
  top: 8rem;
  left: $margin;
  padding: 0;
}

.u-close-right {
  width: 3.5rem;
  height: 3.5rem;
  cursor: pointer;

  z-index: 101;
  position: absolute;
  top: $margin;
  left: calc(#{$slider-size} - #{$margin} - 3.5rem);

  @include tablet {
    top: $margin;
    left: $margin;
  }
}

.hidden {
  display: none;
}
