.profile {
  visibility: hidden;

  &_container {
    width: $slider-size;
    height: 100vh;
    padding: 12rem 0;

    display: grid;
    place-items: center;

    background: rgba($color-black, 0.5);

    @include tablet {
      width: 100vw;
    }
  }

  &_user {
    &-container {
      width: 17rem;
      height: 17rem;
      margin-bottom: 3rem;

      border-radius: 50%;
      border: 2px solid $color-purple;
      background: transparent;
    }
    &-icon {
      width: 100%;
      height: 62%;
      transform: translateY(2rem);
    }
    &-name {
      font-size: 3rem;
    }
    &-points {
      text-align: center;
      color: $color-mint;
      font-weight: 400;
    }
  }

  &_settings {
    &-icon {
      display: inline-block;
      width: 3.5rem;
      height: 3.5rem;
      margin-right: 0.7rem;
      transform: translateY(0.5rem);
    }

    a {
      color: $color-purple !important;
      transform: all 0.2s ease-in;
      font-size: 3rem !important;
      &:hover {
        color: $color-mint !important;
      }
    }
  }

  &_friends {
    &-icon {
      display: inline-block;
    }

    a {
      color: $color-purple !important;
    }
  }
}

#profile:target {
  visibility: visible;
}
